import React, { useEffect, useState } from "react";
import { observer } from "mobx-react-lite";
import { useLocation, useRouteMatch } from "react-router";
import { loadStripe } from "@stripe/stripe-js";
import {
  EmbeddedCheckoutProvider,
  EmbeddedCheckout,
} from "@stripe/react-stripe-js";
import { Route, Switch } from "react-router";

import store, { BASE_URL } from "@/MST/store";

import styles from "./Billing.module.scss";
import axios from "axios";

const stripePromise = loadStripe(
  "pk_test_51Nxd5pEafHQOYw4BGVCREVAXfGdqJ1waIwTd9TT9cWrP1dI8b9rkgaTV25WSRbVGzQFx9YcYDubV8WGF1Y0zNKYf00E2dA3E0t"
);

const BillingStatus: React.FC = () => {

  const {userStripeCustomerId} = store.user;

  const portalRedirect = async () => {
    axios.post(BASE_URL + '/user/customerPortal', {userStripeCustomerId})
  }

  return (
    <div>
      User is registered in Stripe.{" "}
     
        <button type="button" onClick={portalRedirect}>Manage Billing</button>
     
    </div>
  );
};

/** This route renders for the client that does not yet have payment method registeres with Stripe.
 * So he registeres here his credit card via Stripe Elements.
 */
const CheckoutForm: React.FC = () => {
  const { createSubscription } = store.user;
  const { currentProjectId } = store.interface;
  const [clientSecret, setClientSecret] = useState("");

  useEffect(() => {
    // Create a Checkout Session as soon as the page loads.
    createSubscription(currentProjectId).then((secret) =>
      setClientSecret(secret)
    );
  }, []);

  const options = { clientSecret };

  return (
    <div id="checkout">
      {clientSecret && (
        <EmbeddedCheckoutProvider stripe={stripePromise} options={options}>
          <EmbeddedCheckout />
        </EmbeddedCheckoutProvider>
      )}
    </div>
  );
};

/** This route renders after the payment session completed, the Stripe did the redirect from the payment page
 * of the app and the browser tab was reloaded - so all the state was lost. Retrieving the selected project id
 * from the url query string.
 */
const CheckoutResult: React.FC = () => {
  const [sessionResult, setSessionResult] = useState<any>();
  const { getPaymentSessionStatus, getProjectById } = store.user;
  const { currentProjectId, setCurrentProjectId } = store.interface;
  const { search } = useLocation();

  useEffect(() => {
    if (search) {
      const urlParams = new URLSearchParams(search);
      const session_id = urlParams.get("session_id");
      const projectId = urlParams.get("projectId") as string;
      if (!session_id || !projectId) return;
      getPaymentSessionStatus({ session_id, projectId }).then(
        (sessionResult) => {
          setSessionResult(sessionResult);
          setCurrentProjectId(projectId || "");
          if (sessionResult.status === "complete") {
            const project = getProjectById(projectId);
            // This might be a not 100% right presumption, but decided to set the project as active locally
            // as soon as the checkout session proved to be success. The real database change happens when
            // the backend gets the webhook call from Stripe with checkout.session.completed event.
            project?.setProjectIsActive(true);
          }
        }
      );
    }
  }, [search]);

  return sessionResult ? (
    <div className={styles.checkoutResultMessage}>
      Checkout result for project :
      <p>Project name : {getProjectById(currentProjectId)?.projectName}</p>
      <p>Project id : {currentProjectId}</p>
      <p>
        Result :
        {sessionResult.status === "complete"
          ? "Payment successful"
          : "Payment failed"}
      </p>
    </div>
  ) : null;
};

const Billing: React.FC = () => {
  const { path } = useRouteMatch();
  const { userStripeCustomerId } = store.user;

  return (
    <Switch>
      <Route
        exact
        path={path}
        component={userStripeCustomerId ? BillingStatus : CheckoutForm}
      />
      <Route path={`${path}/checkoutResult`} component={CheckoutResult} />
    </Switch>
  );
};

export default observer(Billing);
