import { lazy, Suspense, useEffect, useState } from "react";
import { observer } from "mobx-react-lite";
import {
  Switch,
  Route,
  useRouteMatch,
  Redirect,
  useLocation,
} from "react-router-dom";
import { ProjectStatusLabel, Spinner } from "@/components";
import store from "@/MST/store";

import { UserProjectType } from "@/MST/user";

import styles from "./Cabinet.module.scss";

const Projects = lazy(
  () => import("@/views/Cabinet/Projects" /* webpackChunkName: "Projects" */)
);
const Billing = lazy(
  () => import("@/views/Cabinet/Billing" /* webpackChunkName: "Billing" */)
);
const Settings = lazy(
  () => import("@/views/Cabinet/Settings" /* webpackChunkName: "Settings" */)
);
const Stats = lazy(
  () => import("@/views/Cabinet/Stats" /* webpackChunkName: "Stats" */)
);

type Props = {};

const Cabinet: React.FC<Props> = () => {
  const { url } = useRouteMatch();
  const { pathname } = useLocation();
  const {
    interface: { currentProjectId },
    user: { getProjectById },
  } = store;

  const [currentProject, setCurrentProject] =
    useState<UserProjectType | null>();

  useEffect(() => {
    setCurrentProject(getProjectById(currentProjectId));
  }, [currentProjectId]);

  return (
    <div className={"page"}>
      <div className={"pageContent"}>
        {currentProject ? (
          <div className={styles.projectTitle}>
            <h1 className={styles.projectName}>
              {"Project name : " + currentProject.projectName}
              <ProjectStatusLabel
                isActive={!!currentProject.subscription?.isActive}
              />
            </h1>
            <span className={styles.projectId}>
              {"Project id : " + currentProject._id}
            </span>
          </div>
        ) : null}
        <Suspense fallback={<Spinner />}>
          <Switch>
            <Redirect from="/:url*(/+)" to={pathname.slice(0, -1)} />{" "}
            {/** Removing trailing slashes from the URL*/}
            <Route path={`${url}/projects`} component={Projects} />
            <Route path={`${url}/stats`} component={Stats} />
            <Route path={`${url}/billing`} component={Billing} />
            <Route path={`${url}/settings`} component={Settings} />
            <Redirect to={`${url}/projects`} />{" "}
            {/** For all the wrong routes. */}
          </Switch>
        </Suspense>
      </div>
    </div>
  );
};

export default observer(Cabinet);
