import { observer } from "mobx-react-lite";
import { useHistory } from "react-router";
import Button from "../elements/Button";
import store from "@/MST/store";

import styles from "./ProjectTile.module.scss";

import { UserProjectType } from "@/MST/user";
import ProjectStatusLabel from "../ProjectStatusLabel";

type Props = UserProjectType & { onDelete: (projectId: string) => void };

const ProjectTile: React.FC<Props> = ({
  _id,
  projectName,
  projectCreationDate,
  projectURL,
  subscription,
  onDelete,
}) => {
  const history = useHistory();
  const date = new Date(projectCreationDate || "").toLocaleDateString(
    undefined,
    {
      weekday: "long",
      year: "numeric",
      month: "long",
      day: "numeric",
    }
  );
  const { setCurrentProjectId } = store.interface;

  const onProjectClick = () => {
    setCurrentProjectId(_id || "");
    history.replace("/cabinet/settings");
  };

  const onDeleteClick = (e: React.MouseEvent) => {
    onDelete(_id || "");
  };

  return (
    <div className={styles.projectTile}>
      <div className={styles.projectDetails} onClick={onProjectClick}>
        <div className={styles.wrapper}>
          <div className={styles.projectName}>
            {projectName}
            <ProjectStatusLabel isActive={!!subscription?.isActive} />
          </div>
          <div className={styles.projectDetailField}>URL: {projectURL}</div>
          <div className={styles.projectDetailField}>Created: {date}</div>
        </div>
      </div>

      <div className={styles.projectControls}>
        <Button
          icon="icon_delete"
          text={"Delete project"}
          onClick={onDeleteClick}
        />
      </div>
    </div>
  );
};

export default observer(ProjectTile);
