import { observer } from "mobx-react-lite";
import { Button, Spinner } from "@/components";
import { ProjectTile } from "@/components/ProjectTile";
import store from "@/MST/store";

import styles from "./Projects.module.scss";
import { useEffect } from "react";

const Projects: React.FC = () => {
  const { userProjects } = store.user;
  const { setModalType, setCurrentProjectId, setDeleteProjectId, isLoading } = store.interface;

  useEffect(() => setCurrentProjectId(""), []);

  const onCreateProjectClick = () => {
    setModalType("projectCreate");
  };

  const onDeleteProjectClick = (projectId: string) => {
    setDeleteProjectId(projectId);
    setModalType("projectDelete");
  };

  return (
    <div className={styles.projectsContainer}>
      <Button
        icon="icon_plus"
        text={"Add project"}
        className={styles.addProjectButton}
        onClick={onCreateProjectClick}
      />
      <div className={styles.projectTilesWrapper}>
        {userProjects.length
          ? userProjects.map((project) => (
              <ProjectTile
                key={project._id}
                {...project}
                onDelete={onDeleteProjectClick}
              />
            ))
          : "You dont have any projects yet."}
      </div>

      {isLoading ? <Spinner /> : null}
    </div>
  );
};

export default observer(Projects);
