import { useEffect, useState } from "react";
import { observer } from "mobx-react-lite";
import { Link } from "react-router-dom";
import { Button, Spinner, DemoWidget } from "@/components";
import { validateProjectName, validateURL } from "@/utils/validators";

import store from "@/MST/store";

import { UserProjectType } from "@/MST/user";

import styles from "./Settings.module.scss";

const ProjectPage: React.FC = () => {
  const [currentProject, setCurrentProject] = useState<UserProjectType>();
  const [projectName, setProjectName] = useState("");
  const [projectURL, setProjectURL] = useState("");
  const [widgetHeaderColor, setWidgetHeaderColor] = useState("");
  const [widgetBackgroundColor, setWidgetBackgroundColor] = useState("");
  const [widgetBorderRadius, setWidgetBorderRadius] = useState("0");
  const [widgetBorderColor, setWidgetBorderColor] = useState("");
  const [widgetBorderWidth, setWidgetBorderWidth] = useState("0");
  const [filesList, setFilesList] = useState<File[]>([]);
  const [error, setError] = useState("");

  const {
    user: {
      _id: userId,
      getProjectById,
      saveProjectSettings,
      ingestKnowledgeBase,
    },
    interface: {
      isLoading,
      spinnerMessage,
      currentProjectId,
      setSpinnerMessage,
      pushNotification,
      setIsLoading,
      setModalType,
    },
  } = store;

  useEffect(() => {
    const project = getProjectById(currentProjectId);
    if (!project) return;
    const {
      projectName,
      projectURL,
      widgetSettings: {
        widgetHeaderColor,
        widgetBackgroundColor,
        widgetBorderColor,
        widgetBorderWidth,
        widgetBorderRadius,
      },
    } = project;

    setCurrentProject(project);
    setProjectName(projectName || "");
    setProjectURL(projectURL || "");
    setWidgetHeaderColor(widgetHeaderColor || "");
    setWidgetBackgroundColor(widgetBackgroundColor || "");
    setWidgetBorderColor(widgetBorderColor || "");
    setWidgetBorderWidth(widgetBorderWidth || "");
    setWidgetBorderRadius(widgetBorderRadius || "");
  }, []);

  const getScriptCode = () => {
    return `   <script
    id="clarify_bot_widget"
    data-userId="${userId}"
    data-projectId="${currentProjectId}"
    src="https://content-insight-backend-c244a3d2147a.herokuapp.com/widget/getWidget"
    data-backendURL="https://content-insight-backend-c244a3d2147a.herokuapp.com"
   ></script>`;
  };

  const copyScriptCode = () => {
    window.navigator.clipboard.writeText(getScriptCode()).then(() =>
      pushNotification({
        type: "info",
        message: "Script copied to clipboard.",
      })
    );
  };

  const onInputChange =
    (type: string) => (e: React.ChangeEvent<HTMLInputElement>) => {
      switch (type) {
        case "projectName": {
          setProjectName(e.target.value);
          break;
        }
        case "projectURL": {
          setProjectURL(e.target.value);
          break;
        }
        case "widgetHeaderColor": {
          setWidgetHeaderColor(e.target.value);
          break;
        }
        case "widgetBackgroundColor": {
          setWidgetBackgroundColor(e.target.value);
          break;
        }
        case "widgetBorderColor": {
          setWidgetBorderColor(e.target.value);
          break;
        }
        case "widgetBorderWidth": {
          setWidgetBorderWidth(e.target.value);
          break;
        }
        case "widgetBorderRadius": {
          setWidgetBorderRadius(e.target.value);
          break;
        }
      }
    };

  const validateMetadata = () => {
    if (!validateProjectName(projectName)) {
      setError("projectNameError");
      return false;
    }
    if (!validateURL(projectURL)) {
      setError("projectURLError");
      return false;
    }
    return true;
  };

  const onSaveMetadataClick = async () => {
    if (!validateMetadata()) return;
    setError("");
    const updatedProject = {
      ...currentProject,
      projectName,
      projectURL,
    };
    setIsLoading(true);
    setSpinnerMessage("Updating project metadata...");
    await saveProjectSettings(updatedProject);
    setIsLoading(false);
  };

  const resetMetadata = () => {
    setProjectName(currentProject?.projectName || "");
    setProjectURL(currentProject?.projectURL || "");
  };

  const onSaveWidgetSettingsClick = async () => {
    setIsLoading(true);
    setSpinnerMessage("Updating widget settings...");
    const updatedProject = {
      ...currentProject,
      widgetSettings: {
        widgetHeaderColor,
        widgetBackgroundColor,
        widgetBorderRadius,
        widgetBorderColor,
        widgetBorderWidth,
      },
    };
    await saveProjectSettings(updatedProject);
    setIsLoading(false);
  };

  const resetWidgetSettings = () => {
    if (!currentProject) return;
    const { widgetSettings } = currentProject;
    setWidgetHeaderColor(widgetSettings?.widgetHeaderColor || "");
    setWidgetBackgroundColor(widgetSettings?.widgetBackgroundColor || "");
    setWidgetBorderColor(widgetSettings?.widgetBorderColor || "");
    setWidgetBorderWidth(widgetSettings?.widgetBorderWidth || "");
    setWidgetBorderRadius(widgetSettings?.widgetBorderRadius || "");
  };

  const handleFilesAdd = (e: any) => {
    console.log("handleFilesAdd");
    e.preventDefault();
    e.stopPropagation();
    // defining if we added file through dialogue window - or drag'ndrop
    try {
      const files: FileList =
        e.type === "drop" ? e.dataTransfer.files : e.target.files;
      const newFilesList = [...filesList];
      for (let i = 0; i < files.length; i += 1) {
        if (files[i].type !== "text/plain") continue;
        newFilesList.push(files[i]);
      }
      setFilesList(newFilesList);
    } catch (error: any) {
      pushNotification({
        type: "error",
        message:
          "Error while loading files from local drive. \n" + error.message,
      });
    }
  };

  const onIngestKBClick = async () => {
    setIsLoading(true);
    setSpinnerMessage("Ingesting knowledge base");
    const formData = new FormData();
    filesList.forEach((file) => {
      formData.append("dataFiles", file);
    });

    formData.append("projectId", currentProject?._id || "");
    formData.append("projectName", currentProject?.projectName || "");

    await ingestKnowledgeBase(formData);
    setFilesList([]);
  };

  const onDeleteKBClick = () => {
    setSpinnerMessage("Deleting knowledge base");
    setModalType("proejctKBDelete");
    setFilesList([]);
  };

  if (!currentProject) return <div>Project not found.</div>;

  return (
    <div className={styles.projectPageWrapper}>
      <div className={styles.projectHeader}>
        <Link
          to={"/cabinet/projects"}
          style={{ display: "block", width: "fit-content" }}
        >
          <Button icon="icon_arrow_left" text={"To projects list"} />
        </Link>
      </div>

      <div className={styles.projectPage}>
        <div className={styles.projectPageSection}>
          <div className={styles.sectionHeader}>
            <h2>Widget settings</h2>
            <Button
              disabled={isLoading}
              text={"Discard"}
              icon={"icon_close"}
              onClick={resetWidgetSettings}
            />
            <Button
              disabled={isLoading}
              text={"Save"}
              icon={"icon_save"}
              onClick={onSaveWidgetSettingsClick}
            />
          </div>

          <div className={styles.sectionContent + " " + styles.widgetSettings}>
            <div className={styles.widgetControls}>
              {" "}
              <label className={styles.color}>
                Header color
                <input
                  type="color"
                  value={widgetHeaderColor}
                  onChange={onInputChange("widgetHeaderColor")}
                />
              </label>
              <label className={styles.color}>
                Background color
                <input
                  type="color"
                  value={widgetBackgroundColor}
                  onChange={onInputChange("widgetBackgroundColor")}
                />
              </label>
              <label className={styles.color}>
                Border color
                <input
                  type="color"
                  value={widgetBorderColor}
                  onChange={onInputChange("widgetBorderColor")}
                />
              </label>
              <label className={styles.range}>
                Border width, px
                <input
                  type="range"
                  min={0}
                  max={6}
                  value={widgetBorderWidth}
                  onChange={onInputChange("widgetBorderWidth")}
                />
              </label>
              <label className={styles.range}>
                Border radius, px
                <input
                  type="range"
                  min={0}
                  max={30}
                  value={widgetBorderRadius}
                  onChange={onInputChange("widgetBorderRadius")}
                />
              </label>
            </div>
            <div className={styles.demoWidgetWrapper}>
              <DemoWidget
                settings={{
                  projectId: currentProjectId,
                  widgetHeaderColor,
                  widgetBackgroundColor,
                  widgetBorderRadius,
                  widgetBorderColor,
                  widgetBorderWidth,
                }}
              />
            </div>
          </div>
        </div>
        <div className={styles.projectPageSection}>
          <div className={styles.sectionHeader}>
            <h2>Knowledge base</h2>
            <div className={styles.buttonsWrapper}>
              <Button
                disabled={isLoading || !filesList.length}
                text={"Ingest"}
                icon={"icon_edit"}
                onClick={onIngestKBClick}
              />
              <Button
                disabled={
                  isLoading || !currentProject?.projectIngestedData?.length
                }
                text={"Delete"}
                icon={"icon_delete"}
                onClick={onDeleteKBClick}
              />
            </div>
          </div>
          <div className={styles.sectionContent + " " + styles.knowledgeBase}>
            <div>
              <label>List of knowledgebase files</label>
              <div className={styles.knowledgeBaseList}>
                <ul className={styles.filesList}>
                  {currentProject?.projectIngestedData?.map(
                    ({ fileName, size }) => (
                      <li key={fileName}>
                        <span>{fileName}</span>
                        <span>
                          {Number.parseFloat(size || "").toFixed(2)} kb
                        </span>
                      </li>
                    )
                  )}
                </ul>
              </div>
            </div>
            <div>
              <label>Ingest files</label>

              <div
                className={styles.knowledgeBaseUploadForm}
                onDragOver={(e) => {
                  e.preventDefault();
                  e.stopPropagation();
                }}
              >
                {" "}
                <input
                  title="files to upload"
                  type="file"
                  accept=".txt"
                  multiple={true}
                  onDragEnd={handleFilesAdd}
                  onChange={handleFilesAdd}
                />
                {filesList?.length ? (
                  <ul className={styles.filesList}>
                    {Array.from(filesList).map(({ name, size }) => {
                      return (
                        <li key={name + size}>
                          <span>{name}</span>
                          <span>{size}</span>
                        </li>
                      );
                    })}
                  </ul>
                ) : (
                  <div className={styles.dragndropMessage}>
                    Drag'n'drop txt files or click the area for dialog window.
                    Files are adding up to the ones that are already in
                    knowledge base.
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>{" "}
        <div className={styles.projectPageSection}>
          <div className={styles.sectionHeader}>
            <h2>Metadata</h2>
            <Button
              disabled={isLoading}
              text={"Discard"}
              icon={"icon_close"}
              onClick={resetMetadata}
            />
            <Button
              disabled={isLoading}
              text={"Save"}
              icon={"icon_save"}
              onClick={onSaveMetadataClick}
            />
          </div>
          <div className={styles.sectionContent}>
            <label>
              Project name
              <span className="hintText">
                leters, numbers, dashes are allowed
              </span>
              <input
                type="text"
                className={error === "projectNameError" ? "error" : ""}
                placeholder={"project-name"}
                value={projectName}
                onChange={onInputChange("projectName")}
              />
            </label>
            <label>
              Project URL for the widget usage
              <input
                type="url"
                className={error === "projectURLError" ? "error" : ""}
                placeholder={"https://example.com"}
                value={projectURL}
                onChange={onInputChange("projectURL")}
              />
            </label>
          </div>
        </div>
        <div className={styles.projectPageSection}>
          <div className={styles.sectionHeader}>
            <h2>Client script</h2>
            <Button
              disabled={isLoading}
              text={"Copy"}
              icon={"icon_copy"}
              onClick={copyScriptCode}
            />
          </div>
          <div className={styles.sectionContent}>
            <code className={styles.scriptCode}>{getScriptCode()}</code>{" "}
          </div>
        </div>
        {isLoading ? <Spinner text={spinnerMessage} /> : null}
      </div>
    </div>
  );
};

export default observer(ProjectPage);
